@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  background: #eee5;
}

header {
  background-color: yellow;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
  padding: 16px 32px;
  margin: auto auto;
  display: flex;
  justify-content: flex-start;
  background-color: #f5ba13;
}

header h1 {
  font-size: 40px;

  color: #fff;

  font-family: "McLaren" cursive;
  padding: 0 10px 0 10px;
}

header img {
  padding: 0 10px 0 10px;
  width: 60px;
}

.container {
  display: flex;

  flex-wrap: wrap;
  align-items: center;
}

.note {
  background: #fff;
  border-radius: 7px;
  box-shadow: 0 2px 5px #ccc;
  padding: 10px;
  width: 240px;
  margin: 16px;
  height: 160px;
}
.note h1 {
  margin-bottom: 20px;
}

footer {
  background-color: yellow;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
  bottom: 0;
  position: fixed;
  padding: 5px 30px;
  width: 100%;

  background-color: #f5ba13;
  display: flex;
  justify-content: space-around;
}
.addNewNote {
  display: flex;
  align-items: center;
  justify-content: center;
}
.addNewNote textarea,
input {
  width: 100%;
  border: none;
  padding: 4px;
  outline: none;
  font-size: 1.2em;
  font-family: inherit;
  resize: none;
}
form {
  margin-top: 20px;
  background: #fff;
  padding: 15px;
  border-radius: 7px;
  box-shadow: 0 1px 5px rgb(138, 137, 137);
}

button {
  border-radius: 7px;
  width: 100px;
  height: 35px;
  border: gray;
  padding: 3px;
  margin: 5px;
}
.savedNotes {
  display: flex;
  flex-wrap: wrap;
}
